<script>
import iFixed from 'components/fixed'
export default {
    computed: {
        banner(){
            return this.meeting.settings && this.meeting.settings.home && this.meeting.settings.home.index_banner ? this.meeting.settings.home.index_banner : '';
        },
    },
    data(){
        return{
            viewPic: '',
        }
    },
    components: {
        iFixed
    },
    methods: {
        goForm() {
            this.$router.push({
                name: 'register_form2',
                query: {
                    mid: this.meeting.id
                }
            })
        },
        scrollTo(id) {
            $('.fixed-content').animate({
                scrollTop: $("#" + id).offset().top
            }, 500);
        },
        view(url) {
            this.viewPic = url;
        }
    },
    mounted() {
        this.$store.commit('SPIN')
    }
}
</script>
<style lang="less" scoped>
    @import "../../../assets/style/home/index.less";
    @import "./style/oriflame.less";
    .nav {
        height: 120px;
        .logo {
            float: left;
            padding: 30px 120px;
        }
        ul {
            float: left;
            margin: 0;
            line-height: 120px;
            font-size: 14px;
            li {
