            right: 0;
            bottom: 26px;
            width: 240px;
            background: @ori-green;
            color: #fff;
            text-align: center;
            padding: 16px 26px;
            font-size: 30px;
            cursor: pointer;
            &:hover {
                color: #fff;
                background: @ori-greenDeep;
            }
            .time {
                font-size: 12px;
                margin-top: 6px;
            }
        }
    }
    .main {
        .block {
            padding: 80px 16%;
            .title {
                font-size: 36px;
                font-weight: 600;
                margin-bottom: 40px;
            }
            .schedule {
                float: left;
                width: 50%;
            }
            .photo {
                float: right;
                .item {
                    margin-bottom: 40px;
                    .intro {
                        text-align: right;
                        line-height: 250%;
                    }
                }
            }
            .col-2 {
                float: left;
                width: 50%;
                @media (max-width: 480px) {
                    width: 100%;
                }
            }
            &.grey {
                background: #f9f9f9;
            }
            &::after {
                content: '';
                display:block;
                clear: both;
            }
        }
    }
    .schedule {
        min-width:320px;
        font-size:12px;
        li {
            line-height: 24px;
            clear: both;
            position:relative;
            .l {
                display: inline-block;
                width: 50%;
                text-align: right;
                padding-right: 28px;
            }
            .r {
                display:inline-block;
                padding-left: 24px;
            }
            .time {
                color: @ori-fontGrey;
            }
            &::before {
                content: '';
                display: block;
                width: 6px;
                height: 6px;
                background-color: #fff;
                border: 1px solid @ori-green;
                border-radius: 50%;
                position: absolute;
                left: 50%;
                top: 50%;
                margin-left: -2px;
                margin-top: -2px;
                z-index:1;
             }
            &::after {
                content: '';
                display: block;
                width: 0;
                border: 1px solid #f3f3f3;
                position: absolute;
                left: 50%;
                top: 0;
                bottom: 0;
                z-index: 0;
            }
            &:first-child {
                &::after {
                     top: 50%;
                }
                &.date .box {
                     margin-top: 0;
                }
            }
            &:last-child {
                &::after {
                     bottom: 50%;
                }
            }
            &.date {
                .box {
                    margin: 24px 0 12px 0;
                    background-color: @ori-green;
                    padding: 6px;
                    border-radius: 2px;
                    text-align: center;
                    line-height: 18px;
                    color:#fff;
                    float:right;
                    min-width: 120px;
                }
                &::before {
                    width: 8px;
                    height: 8px;
                    margin-left: -3px;
                    margin-top: -3px;
                    background-color: @ori-green;
                }
            }
        }
    }
    .alert {
        color: #f00000 !important;
    }
    .picView {
        position: fixed;
        z-index: 50;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(0,0,0,0.6);
        cursor: pointer;
        .ctn {
            position: absolute;
            top: 20%;
            left: 20%;
            right: 20%;
            bottom: 20%;
            text-align:center;
            img {
                max-width: 100%;
                max-height: 100%;
            }
        }
    }
</style>



